











































































































import { Component, Vue } from "vue-property-decorator";
import { SweetAlertOptions } from "sweetalert2";

@Component
export default class App extends Vue {
  alertExample0() {
    alert("This is normal alert.");
  }
  alertExample1() {
    this.$alert("This is simple and beautiful alert.");
  }
  alertExample2() {
    this.$alert("This is simple and beautiful alert with title.", "Example");
  }
  alertExample3() {
    this.$alert(
      "This is simple and beautiful alert with icon.",
      "Success",
      "success"
    );
  }
  alertExample4() {
    this.$alert(
      "This is simple and beautiful alert with icon.",
      "Warning",
      "warning"
    );
  }

  confirmExample0() {
    confirm("This is normal confirm.");
  }
  confirmExample1() {
    this.$confirm("This is beautiful confirm.", "Confirm");
  }
  confirmExample2() {
    this.$confirm(
      "This is beautiful confirm with question icon.",
      "Question",
      "question"
    )
      .then(r => {
        console.log(r);
      })
      .catch(() => {
        console.log("OK not selected.");
      });
  }
  confirmExample3() {
    this.$confirm(
      "This is beautiful confirm with error icon.",
      "Error",
      "error"
    )
      .then(r => {
        console.log(r);
        if (r) this.$alert("OK selected.");
      })
      .catch(() => {
        console.log("OK not selected.");
      });
  }

  promptExample0() {
    prompt("Input your name", "John Doe");
  }

  promptExample1() {
    this.$prompt("Input your name", "John Doe").then(r => {
      if (r) this.$alert(r, "Your name is:", "success");
    });
  }

  promptExample2() {
    this.$prompt("Input your name", "John Doe", "Example", "question").then(
      r => {
        if (r) this.$alert(r, "Your name is:", "success");
      }
    );
  }

  advancedExample() {
    const options: SweetAlertOptions = {
      title: "<strong>Advanced</strong> &nbsp; example.",
      text: "This will be closed after 3 seconds.",
      footer: "<a href>Check out sweetalert2 documentation.</a>",
      type: "success",
      showCancelButton: true,
      confirmButtonText: "<i class='fa fa-thumbs-up'></i> Great!",
      cancelButtonText: "No, cancel!",
      reverseButtons: true,
      timer: 3000,
      width: 500,
      animation: false,
      customClass: {
        popup: "animated tada"
      },
      padding: "3em",
      background: "#fff",
      backdrop: `
        rgba(0,0,123,0.4)
        center left
        no-repeat
      `
    };

    this.$fire(options).then(r => {
      if (r.value) this.$alert(r.value, "Result");
    });
  }
}
